import "core-js/modules/es.array.push.js";
import { getUser } from '@/request/api/home';
export default {
  data() {
    return {
      phone: '',
      password: ''
    };
  },

  methods: {
    Login: async function () {
      let res = await this.$store.dispatch('getLogin', {
        phone: this.phone,
        password: this.password
      });
      console.log(res);

      if (res.data.code == 200) {
        this.$store.commit('updataIsLogin', true);
        this.$store.commit('updataToken', res.data.token); // 获取返回的个人信息

        let result = await getUser(res.data.account.id);
        console.log(result);
        this.$store.commit('updataUserDetail', result); // 如果返回的code是200就跳转到个人中心

        this.$router.push('/infoUser');
      } else {
        alert("手机号码/密码错误");
        this.password = '';
      }
    }
  }
};